
import { PropType, DefineComponent } from 'vue'
export default {
    props: {
        options: {
        },
        description: {
            type: String
        },
        modelValue: {
            type: String
        },
        readonly: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        opts: (vm: DefineComponent) => {
            if (vm.options instanceof Array) {
                return vm.options.reduce((prev, v, idx) => {
                    return {...prev, [v]: v}
                }, {})
            } else {
                return vm.options
            }
        }
    }  
}
